* {
  box-sizing: border-box; }

@media (max-width: 600px) {
  .grid-container {
    grid-template-areas: 'header header header header header header' 'left left left left left left' 'middle middle middle middle middle middle' 'right right right right right right' 'footer footer footer footer footer footer'; } }

body {
  margin: 0;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ffffff !important;
  background: var(--background) !important;
  color: #121312 !important;
  color: var(--foreground) !important;
  font-family: "Assistant", sans-serif !important;
  font-family: var(--ff-body) !important;
  font-size: 18px !important;
  line-height: 1.6 !important; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

:root {
  --clr-light: #ffffff;
  --clr-dark: #121312;
  --clr-primary: #dbffa2;
  --clr-secondary: #c3fcf2;
  --clr-accent: #ff7750;
  --foreground: var(--clr-dark);
  --background: var(--clr-light);
  --ff-title: "Playfair Display", serif;
  --ff-body: "Assistant", sans-serif; }

.darkmode {
  --clr-light: #ffffff;
  --clr-dark: #121312;
  --clr-primary: #202302;
  --clr-secondary: #00100d;
  --clr-accent: #ff7750;
  --foreground: var(--clr-light);
  --background: var(--clr-dark); }

.array-container {
  position: absolute;
  left: 0;
  /* top: 100px; */ }

.array-bar {
  width: 5px;
  display: inline-block;
  margin: 0 10px;
  position: relative; }

.array-bar .tooltiptext {
  visibility: hidden;
  width: 60px;
  background: var(--background) !important;
  color: var(--foreground) !important;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  font-weight: bold;
  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: -10px;
  left: 20px; }

.array-bar:hover .tooltiptext {
  visibility: visible; }

/* Style the header */
.header-sort {
  grid-area: header;
  color: var(--foreground) !important;
  padding: 10px;
  text-align: center; }

.header-sort > div {
  display: inline-block;
  display: -moz-inline-box;
  *display: inline;
  /* For IE7 */
  zoom: 1;
  /* Trigger hasLayout */
  width: 20%;
  text-align: center; }

/* The grid container */
.grid-container-sort {
  min-height: calc(100vh - 70px);
  display: grid;
  grid-template-areas: "header header header" "left middle  right" "footer footer footer";
  grid-column-gap: 5px;
  border: 10px solid white;
  grid-template-columns: 1fr 8fr 1fr;
  /* grid-column-gap: 10px; - if you want gap between the columns */ }

.left-sort,
.middle-sort,
.right-sort {
  border: 1px solid #222;
  padding: 5px;
  background-color: white;
  /*height: 300px; /* Should be removed. Only for demonstration */ }

/* Style the left column */
.left-sort {
  grid-area: left; }

/* Style the middle column */
.middle-sort {
  grid-area: middle;
  min-height: 60vh;
  min-width: 60vw;
  max-width: 100vw; }

/* Style the right column */
.right-sort {
  grid-area: right; }

.input-range {
  margin: 5 5px; }

.input-range:hover .tooltiptext {
  visibility: visible; }

.input-range .tooltiptext {
  font-weight: bold;
  visibility: hidden;
  width: 60px;
  background: var(--background) !important;
  color: var(--foreground) !important;
  text-align: center;
  border-radius: 50%;
  padding: 5px 0;
  font-weight: bold;
  /* Position the tooltip */
  position: absolute;
  z-index: 1; }

p {
  margin-right: 10px; }

/* Style the footer */
.footer {
  grid-area: footer;
  background: var(--background) !important;
  color: var(--foreground) !important;
  padding: 10px;
  text-align: center;
  height: 50px; }

#bars::after {
  background: var(--foreground) !important;
  color: var(--background) !important;
  content: " ";
  display: block;
  width: 100%;
  height: 2px; }

#bars::before {
  background: var(--foreground) !important;
  color: var(--background) !important;
  content: " ";
  display: block;
  width: 100%;
  height: 2px; }

.middle hr {
  left: 0;
  margin-top: -15px;
  width: 100%;
  display: inline-block;
  box-sizing: border-box;
  height: 5px;
  border: none;
  background: darkslategray; }

h1 {
  font-family: Lato;
  font-size: 24px;
  font-style: normal;
  font-feature-settings: normal;
  font-variant: normal;
  font-weight: 700;
